html,body{
    margin: 0;
    padding: 0;
    height: 100%;
    color: #657180;
    font-size: 16px;
}
.daily-menu{
    width: 150px;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
    background: #f5f7f9;
}
.daily-menu-item{
    font-size: 18px;
    text-align: center;
    margin: 5px 0;
    padding: 10px 0;
    cursor: pointer;
    border-right: 2px solid transparent;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}
.daily-menu-item:hover{
   background: #e3e8ee;
}
.daily-menu-item.on{
    border-right: 2px solid #3399ff;
 }
.daily-list{
     width: 300px;
     position: fixed;
     top: 0;
     bottom: 0;
     left: 150px;
     overflow: auto;
     border-right: 1px solid #d7dde4;
 }
.daily-date{
     text-align: center;
     margin: 10px 0; 
}
.daily-item{
    display: block;
    color: inherit;
    text-decoration: none;
    padding: 16px;
    overflow: hidden;
    cursor: pointer;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}
.daily-item:hover{
    background: #e3e8ee;
}
.daily-article{
    margin-left: 450px;
    padding: 20px;
}
.daily-menu ul{
    list-style: none;
}
.daily-menu ul li a{
    display: block;
    color: inherit;
    text-decoration: none;
    padding:  5px 0;
    margin: 5px 0;
    cursor: pointer;
}
.daily-menu ul li a:hover,.daily-menu ul li a.on{
    color: #3399ff;
}
.daily-article-title{
    font-size: 28px;
    font-weight: bold;
    color: #222;
    padding: 10px 0;
}
.view-more a{
    display: block;
    cursor: pointer;
    background: #f5f7f9;
    text-align: center;
    color: inherit;
    text-decoration: none;
    padding: 4px 0;
    border-radius: 3px;
}